<template>
	<div class="add">
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
			<el-breadcrumb-item><b></b></el-breadcrumb-item>
			<el-breadcrumb-item :to="{ path: '/goods' }"><b></b></el-breadcrumb-item>
			<el-breadcrumb-item><b></b></el-breadcrumb-item>
		</el-breadcrumb>
		<!--  -->
		<el-card>
			<el-alert title="" type="info" show-icon :closable="false" center></el-alert>
			<!--  -->
			<el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center="">
			  <el-step title=""></el-step>
			  <el-step title=""></el-step>
			  <el-step title=""></el-step>
			  <el-step title=""></el-step>
			  <el-step title=""></el-step>
			  <el-step title=""></el-step>
			</el-steps>
			<!-- tab -->
			<el-form :model="addForm" :rules="addFormules" ref="addFormRef" label-width="100px" label-position="top">
			<el-tabs v-model="activeIndex" :tab-position="'left'" :before-leave="beforeTabLeave" @tab-click="tabClicked">
			    <el-tab-pane label="" name="0">
						<el-form-item label="" prop="goods_cat">
							  <el-cascader
							    v-model="addForm.goods_cat"
							    :options="catelist"
							    :props="cateProps"
							    @change="handleChange">
								</el-cascader>
						</el-form-item>
						<el-form-item label="" prop="goods_name">
						    <el-input v-model="addForm.goods_name"></el-input>
						</el-form-item>
						<el-form-item label="" prop="goods_price">
						    <el-input v-model="addForm.goods_price" type="number"></el-input>
						</el-form-item>
						<el-form-item label="" prop="goods_weight">
						    <el-input v-model="addForm.goods_weight" type="number"></el-input>
						</el-form-item>
						<el-form-item label="" prop="goods_number">
						    <el-input v-model="addForm.goods_number" type="number"></el-input>
						</el-form-item>
					</el-tab-pane>
			    <el-tab-pane label="" name="1">
						<el-form-item v-for="item in manyTableData" :key="item.attr_id" :label="item.attr_name">
							<!--  -->
							<el-checkbox-group v-model="item.attr_vals">
							    <el-checkbox border :label="cb" v-for="(cb, i) in item.attr_vals" :key="i"></el-checkbox>
							  </el-checkbox-group>
						</el-form-item>
					</el-tab-pane>
			    <el-tab-pane label="" name="2">
						<el-form-item v-for="item in onlyTableData" :key="item.attr_id" :label="item.attr_name">
							<el-input v-model="item.attr_vals"></el-input>
						</el-form-item>
					</el-tab-pane>
			    <el-tab-pane label="" name="3">
						<el-upload
						  :action="uploadURL"
						  :on-preview="handlePreview"
						  :on-remove="handleRemove"
						  list-type="picture"
							:headers="headerObj"
							:on-success="handleSuccess">
						  <el-button size="small" type="primary"></el-button>
						  <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
						</el-upload>
					</el-tab-pane>
			    <el-tab-pane label="" name="4">
						<el-form-item label="" prop="goods_introduce">
						    <el-input type="textarea" v-model="addForm.goods_introduce"></el-input>
								<el-button type="primary" @click="add"></el-button>
						</el-form-item>
					</el-tab-pane>
			  </el-tabs>
			</el-form>	
		</el-card>
		
		<!--  -->
		<el-dialog
		  title=""
		  :visible.sync="previewVisible"
		  width="50%">
				<img :src="previewPath" alt="" style="width: 100%;">
		</el-dialog>
	</div>
</template>

<script>
	import _ from 'lodash'
  export default{
    name:"Add",
		data() {
			return {
				// tab
				activeIndex: '0',
				// 
				addForm:{
					goods_name:'',
					goods_price:'',
					goods_weight:'',
					goods_number:'',
					//
					goods_cat:[],
					// 
					pics:[],
					goods_introduce:'',
					attrs:[]
				},
				// 
				addFormules:{
					goods_name:[
						{ required: true, message: '', trigger: 'blur' },
					],
					goods_price:[
						{ required: true, message: '', trigger: 'blur' },
					],
					goods_weight:[
						{ required: true, message: '', trigger: 'blur' },
					],
					goods_number:[
						{ required: true, message: '', trigger: 'blur' },
					],
					goods_cat:[
						{ required: true, message: '', trigger: 'blur' },
					],
					goods_introduce:[
						{ required: true, message: '', trigger: 'blur' },
					]
				},
				//
				catelist:[],
				cateProps:{
					//
					expandTrigger: 'hover',
					label:'cat_name',
					value:'cat_id',
					children:'children'
				},
				// 
				manyTableData:[],
				// 
				onlyTableData:[],
				//URL
				uploadURL:'http://119.23.53.78:8888/api/private/v1/upload',
				//header
				headerObj:{
					Authorization:window.sessionStorage.getItem('token')
				},
				//
				previewPath:'',
				//
				previewVisible:false
				
				
				
				
			}
		},
		created() {
			this.getCateList()
		},
		methods:{
			//
			async getCateList(){
				const {data:res} = await this.$http.get('categories')
				if(res.meta.status !== 200){
					return this.$message.error('')
				}
				this.catelist = res.data
				// console.log(this.catelist)
			},
			//
			handleChange(){
				if(this.addForm.goods_cat.length !== 3 ){
					this.addForm.goods_cat = []
				}
				// console.log(this.addForm.goods_cat)
			},
			// tab false  Promise  reject
			beforeTabLeave(activeName, oldActiveName){
				// console.log(''+oldActiveName)
				// console.log(''+activeName)
				if(oldActiveName == '0' && this.addForm.goods_cat.length !== 3){
					this.$message.error('')
					return false
				}
			},
				// tab 
			async tabClicked(){
				// console.log(this.activeIndex)
				if(this.activeIndex == '1'){
					// 
					const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{params:{sel:'many'}})
					if(res.meta.status !== 200){
						return this.$message.error('')
					}
					res.data.forEach(item => {
						item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(",")
					})
					// console.log(res.data)
					this.manyTableData = res.data
				}else if(this.activeIndex == '2'){
					// 
					const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{params:{sel:'only'}})
					if(res.meta.status !== 200){
						return this.$message.error('')
					}
					// console.log(res.data)
					this.onlyTableData = res.data
				}	
			},
			//
			handlePreview(file){
				// console.log(file)
				this.previewPath = file.url
				this.previewVisible = true
			},
			//
			handleRemove(file){
				// console.log(file)
				// 1.
				const filePath = file.response.data.tmp_path
				// 2.pics,
				const i = this.addForm.pics.findIndex(x => x.pic === filePath)
				// 3.splice,pics
				this.addForm.pics.splice(i, 1)
				// console.log(this.addForm)
			},
			// 
			handleSuccess(response){
				// console.log(response)
				// 1.
				const picInfo = {pic:response.data.tmp_path}
				// 2.pushpics
				this.addForm.pics.push(picInfo)
				// console.log(this.addForm)
			},
			//
			add(){
				// console.log(this.addForm)
				this.$refs.addFormRef.validate(async valid => {
					if(!valid) {
						return this.$message.error('')
					}
					const form = _.cloneDeep(this.addForm)
					form.goods_cat = form.goods_cat.join(',')
					// 
					this.manyTableData.forEach(item => {
						const newInfo = {attr_id:item.attr_id,attr_value:item.attr_vals.join(',')}
						this.addForm.attrs.push(newInfo)
					})
					// 
					this.onlyTableData.forEach(item => {
						const newInfo = {attr_id:item.attr_id,attr_value:item.attr_vals}
						this.addForm.attrs.push(newInfo)
					})
					form.attrs = this.addForm.attrs
					// console.log(form)
					
					// ,
					const {data:res} = await this.$http.post('goods',form)
					if(res.meta.status !== 201){
						return this.$message.error('')
					}
					this.$message.success('')
					this.$router.push('/goods')
				})
			}
			
			
			
		},
		computed:{
			cateId(){
				if(this.addForm.goods_cat.length == 3){
					return this.addForm.goods_cat[2]
				}
				return null
			}
		}
  }
</script>

<style lang="less" scoped>
	.el-checkbox{
		margin: 0.3125rem 0.625rem 0 0 !important;
	}
	.el-button{
		margin-top: 0.9375rem;
	}
</style>
