<template>
	<div class="Roles">
		<!--  -->
		<crumbs>
			<b slot="crumbsa"></b>
			<b slot="crumbsb"></b>
		</crumbs>
		<!--  -->
		<el-card>
			<el-col :span="3">
				<el-button class="jsbutton" type="primary" @click="addRoles = true"></el-button>
			</el-col>
			<!--  -->
			<el-table border :data="rolelist">
				<!--  -->
				<el-table-column type="expand">
					<template v-slot="scope">
						<el-row :class="['bdbottom', i1 == 0 ? 'bdtop' : '', 'vcenter']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
							<!--  -->
							<el-col :span="5">
								<el-tag closable @close="removeRightId(scope.row, item1.id)">{{item1.authName}}</el-tag>
								<i class="el-icon-caret-right"></i>
							</el-col>
							<!--  -->
							<el-col :span="19">
								<!-- for -->
								<el-row :class="[i2 != 0 ? 'bdtop' : '', 'vcenter']" v-for="(item2, i2) in item1.children" :key='item2.id'>
									<el-col :span="6">
										<el-tag type="success" closable @close="removeRightId(scope.row, item2.id)">{{item2.authName}}</el-tag>
										<i class="el-icon-caret-right"></i>
									</el-col>
									<el-col :span="18">
										<el-tag type="warning" v-for="(item3, i3) in item2.children" :key="item3.id" closable @close="removeRightId(scope.row, item3.id)">
											{{item3.authName}}
										</el-tag>
									</el-col>
								</el-row>
							</el-col>
						</el-row>
					</template>
				</el-table-column>
				<!--  -->
				<el-table-column align="center" label='' type='index'></el-table-column>
				<el-table-column align="center" label='' prop="roleName"></el-table-column>
				<el-table-column align="center" label='' prop="roleDesc"></el-table-column>
				<el-table-column align="center" label='' width="300px">
					<template v-slot="scope">
						<el-button @click="showEditRoles(scope.row.id)" size="mini" type="primary" icon="el-icon-edit"></el-button>
						<el-button @click="removeUserId(scope.row.id)" size="mini" type="danger" icon="el-icon-delete"></el-button>
						<el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-card>
		
		<!--  -->
		<el-dialog
		  title=""
		  :visible.sync="addRoles"
		  width="50%"
			@close="addRolesClose">
			<el-form :model="RolesForm" :rules="RolesFormRules" ref="RolesFormRef" label-width="80px">
			  <el-form-item label="" prop="roleName">
			    <el-input v-model="RolesForm.roleName"></el-input>
			  </el-form-item>
				<el-form-item label="" prop="roleDesc">
				  <el-input v-model="RolesForm.roleDesc"></el-input>
				</el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="addRoles = false"> </el-button>
		    <el-button type="primary" @click="addUser"> </el-button>
		  </span>
		</el-dialog>
		<!--  -->
		<el-dialog
		  title=""
		  :visible.sync="editRoles"
		  width="50%"
			@close="editRolesClose">
			<el-form :model="editRolesForm" :rules="RolesFormRules" ref="editRolesFormRef" label-width="80px">
			  <el-form-item label="" prop="roleName">
			    <el-input v-model="editRolesForm.roleName"></el-input>
			  </el-form-item>
				<el-form-item label="" prop="roleDesc">
				  <el-input v-model="editRolesForm.roleDesc"></el-input>
				</el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="editRoles = false"> </el-button>
		    <el-button type="primary" @click="editUser"> </el-button>
		  </span>
		</el-dialog>
		<!--  -->
		<el-dialog
		  title=""
		  :visible.sync="setRightDialogVisible"
		  width="50%"
			@close="setRightDialogClosed">
		  <!--  -->
			<el-tree :data="rightslist" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="setRightDialogVisible = false"> </el-button>
		    <el-button type="primary" @click="allotRights"> </el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script> 
	import Crumbs from '../common/Crumbs.vue'
  export default{
    name:"Roles",
		components:{
			Crumbs
		},
		data(){
			return {
				// 
				rolelist:[],
				// 
				addRoles: false,
				RolesForm:{
					roleName:'',
					roleDesc:''
				},
				RolesFormRules:{
					roleName:[
						{ required: true, message: '', trigger: 'blur' },
						{ min: 3, max: 6, message: ' 3  6 ', trigger: 'blur' }
					],
					roleDesc:[
						{ required: true, message: '', trigger: 'blur' },
						{ min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
					]
				},
				editRolesForm:{},
				// 
				editRoles:false,
				// 
				setRightDialogVisible:false,
				// 
				rightslist:[],
				// 
				treeProps:{
					label:'authName',
					children:'children'
				},
				// id
				defKeys:[],
				//id
				roleId:''
			}
		},
		created() {
			this.getRolesList()
		},
		methods:{
			async getRolesList(){
				const {data:res} = await this.$http.get('roles')
					// console.log(res)
					if(res.meta.status !== 200){
						return this.$message.error('')
					}
					this.rolelist = res.data
					// console.log(this.rolelist)
			},
			addRolesClose(){
				this.$refs.RolesFormRef.resetFields()
			},
			addUser(){
				this.$refs.RolesFormRef.validate(async valid => {
					// console.log(valid)
					if(!valid) return
					const {data:res} = await this.$http.post('roles',this.RolesForm)
					// console.log(res)
					if(res.meta.status !== 201){
						return this.$message.error('')
					}
					this.$message.success('')
					this.addRoles = false
					this.getRolesList()
				})
			},
			async showEditRoles(id){
				const {data:res} = await this.$http.get('roles/' + id)
				// console.log(res)
				if (res.meta.status !== 200) {
					return this.$messag.error('')
				}
				this.editRolesForm = res.data
				this.editRoles = true
			},
			editRolesClose(){
				this.$refs.editRolesFormRef.resetFields()
				this.editRolesForm = {}
			},
			editUser(){
				this.$refs.editRolesFormRef.validate(async valid => {
					// console.log(valid)
					if (!valid) return
					// 
					const {data: res} = await this.$http.put('roles/' + this.editRolesForm.roleId, {roleName: this.editRolesForm.roleName,roleDesc: this.editRolesForm.roleDesc})
					// console.log(res)
					if (res.meta.status !== 200) {
						return this.$message.error('')
					}
					//
					this.editRoles = false
					// 
					this.getRolesList()
					// 
					this.$message.success('')
				})
			},
			async removeUserId(id){
				// 
				const confirmResult = await this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).catch(err => err)
				
				// , confirm
				// , cancel
				// console.log(confirmResult)
				if(confirmResult !== 'confirm'){
					return this.$message.info('')
				}
				
				const {data:res} = await this.$http.delete('roles/' + id)
					if(res.meta.status !== 200){
						return this.$message.error('')
					}
					this.$message.success('')
					this.getRolesList()
			},
			// id
			async removeRightId(role, rightId){
				//
				const confirmResult = await this.$confirm(', ?', '', {
				          confirmButtonText: '',
				          cancelButtonText: '',
				          type: 'warning'
				        }).catch(err => err)
						if(confirmResult !== 'confirm'){
							return this.$message.info('')
						} 
						
					  const {data: res} =	await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
						if(res.meta.status !== 200){
							return this.$message.error('')
						}
						this.$message.success('')
						// this.getRolesList()
						role.children = res.data
			},
			// 
			async showSetRightDialog(role){
				this.roleId = role.id
				// 
				const {data:res} = await this.$http.get('rights/tree') 
				if(res.meta.status !== 200){
					return this.$message.error('')
				}
				// data
				this.rightslist = res.data
				// console.log(this.rightslist)
				
				// id
				this.getLeafKey(role, this.defKeys)
				
				this.setRightDialogVisible = true
			},
			// ,id,defKeys
			getLeafKey(node, arr){
				// nodechildren,
				if(!node.children){
					return arr.push(node.id)
				}
				node.children.forEach(item => this.getLeafKey(item, arr))
			},
			// 
			setRightDialogClosed(){
				// defKeysid
				this.defKeys = []
			},
			//
			async allotRights(){
				const keys = [
					...this.$refs.treeRef.getCheckedKeys(),
					...this.$refs.treeRef.getHalfCheckedKeys()
				]
				// console.log(keys)
				const idStr = keys.join(',')
				// console.log(idStr)
				
				const {data:res} = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
				if(res.meta.status !== 200){
					return this.$message.error('')
				}
				this.$message.success('')
				this.getRolesList()
				this.setRightDialogVisible = false
			}
			
			
		}
  }
</script>

<style lang="less" scoped>
	.jsbutton{
		margin-bottom: 0.9375rem;
	}
	.el-tag{
		margin: 0.4375rem;
	}
	.bdtop{
		border-top: 1px solid #eee;
	}
	.bdbottom{
		border-bottom: 1px solid #eee;
	}
	.vcenter{
		display: flex;
		align-items: center;
	}
</style>
