<template>
	<div class="goods">
		<!--  -->
		<crumbs>
			<b slot="crumbsa"></b>
			<b slot="crumbsb"></b>
		</crumbs>
		<!--  -->
		<el-card>
			<el-row :gutter="15">
				<el-col :span="8">
					<el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodsList">
					  <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
					</el-input>
				</el-col>
				<el-col :span="4">
					<el-button type="primary" @click="goAddpage"></el-button>
				</el-col>
			</el-row>
			
			<!-- table -->
			<el-table :data="goodslist" border="">
				<el-table-column align="center" type="index" label=""></el-table-column>
				<el-table-column align="center" label="" prop="goods_name"></el-table-column>
				<el-table-column align="center" label="()" prop="goods_price" width="95px"></el-table-column>
				<el-table-column align="center" label="" prop="goods_weight" width="70px"></el-table-column>
				<el-table-column align="center" label="" prop="add_time" width="140px">
					<template v-slot="scope">
						{{scope.row.add_time | dateFormat}}
					</template>
				</el-table-column>
				<el-table-column align="center" label="" width="200px">
					<template v-slot="scope">
						<el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
						<el-button type="danger" icon="el-icon-delete" size="mini" @click="removeById(scope.row.goods_id)"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!--  -->
			<el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page="queryInfo.pagenum"
			      :page-sizes="[5, 10, 15, 20]"
			      :page-size="queryInfo.pagesize"
			      layout="total, sizes, prev, pager, next, jumper"
			      :total="total"
						background>
			    </el-pagination>
		</el-card>
	</div>
</template>

<script>
	import Crumbs from '../common/Crumbs.vue'
  export default{
    name:"Goods",
		components:{
			Crumbs
		},
		data() {
			return {
				//
				queryInfo:{
					query:'',
					pagenum: 1,
					pagesize: 10
				},
				//
				goodslist:[],
				// 
				total:0
			}
		},
		created() {
			this.getGoodsList()
		},
		methods:{
			//
			async getGoodsList(){
				const {data:res} = await this.$http.get('goods',{params:this.queryInfo})
				if(res.meta.status !== 200){
					return this.$message.error('')
				}
				// console.log(res.data)
				this.goodslist =  res.data.goods
				this.total = res.data.total
			},
			//
			handleSizeChange(newSize){
				this.queryInfo.pagesize = newSize
				this.getGoodsList()
			},	
			//
			handleCurrentChange(newPage){
				this.queryInfo.pagenum = newPage
				this.getGoodsList()
			},
			//
			async removeById(id){
				const confirmResult =  await this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).catch(err => err)
				
				if(confirmResult !== 'confirm'){
					return this.$message.info('')
				}
				
				const {data:res} = await this.$http.delete(`goods/${id}`)
				console.log(res)
				if(res.meta.status !== 200){
					return this.$message.error('')
				}
				this.$message.success('')
				this.getGoodsList()
			},
			
			goAddpage(){
				this.$router.push('/goods/add')
			}
			
		}
  }
</script>

