<template>
	<div class="Categories">
		<!--  -->
		<crumbs>
			<b slot="crumbsa"></b>
			<b slot="crumbsb"></b>
		</crumbs>
		<!--  -->
		<el-card>
			<el-row>
				<el-col>
					<el-button class="bobutton" type="primary" @click="showAddCate"></el-button>
				</el-col>
			</el-row>
			<!--  -->
			<tree-table :data="catelist" :columns="columns" :selection-type="false" :expand-type="false" show-index border :show-row-hover="false">
				<!--  -->
				<template v-slot:isok="scope">
					<i class="el-icon-success" v-if="!scope.row.cat_deleted"></i>
					<i class="el-icon-error" v-else></i>
				</template>
				<!--  -->
				<template v-slot:order="scope">
					<el-tag size="mini" v-if="scope.row.cat_level == 0"></el-tag>
					<el-tag type="success" size="mini" v-else-if="scope.row.cat_level == 1"></el-tag>
					<el-tag type="warning" size="mini" v-else></el-tag>
				</template>
				<!--  -->
				<template v-slot:opt="scope">
					<el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditCate(scope.row.cat_id)"></el-button>
					<el-button type="danger" icon="el-icon-delete" size="mini" @click="removeCate(scope.row.cat_id)"></el-button>
				</template>
			</tree-table>	
				<!--  -->
				<el-pagination
				      @size-change="handleSizeChange"
				      @current-change="handleCurrentChange"
				      :current-page="queryInfo.pagenum"
				      :page-sizes="[3, 5, 10, 15]"
				      :page-size="queryInfo.pagesize"
				      layout="total, sizes, prev, pager, next, jumper"
				      :total="total">
				    </el-pagination>
		</el-card>
		
		<!--  -->
		<el-dialog
		  title=""
		  :visible.sync="addCateDialogVisible"
		  width="50%"
			@close="addCateDialogClosed">
			<!--  -->
		  <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
		    <el-form-item label=":" prop="cat_name">
		      <el-input v-model="addCateForm.cat_name"></el-input>
		    </el-form-item>
				<el-form-item label=":">
					<!-- options  -->
					<!-- props  -->
				  <el-cascader
				      v-model="selectdeKeys"
				      :options="parentCatrList"
				      :props="cascaderProps"
				      @change="parentCatrChanged"
							clearable></el-cascader>
				</el-form-item>
			</el-form>	
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="addCateDialogVisible = false"> </el-button>
		    <el-button type="primary" @click="addCate"> </el-button>
		  </span>
		</el-dialog>
		<!--  -->
		<el-dialog
		  title=""
		  :visible.sync="editCateDialogVisible"
		  width="50%"
			@close="editCateDialogClosed">
		  <el-form :model="editCateForm" :rules="addCateFormRules" ref="editCateFormRef" label-width="100px">
		    <el-form-item label=":" prop="cat_name">
		      <el-input v-model="editCateForm.cat_name"></el-input>
		    </el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="editCateDialogVisible = false"> </el-button>
		    <el-button type="primary" @click="eidtCate"> </el-button>
		  </span>
		</el-dialog>
		
	</div>
</template>

<script>
	import Crumbs from '../common/Crumbs.vue'
  export default{
    name:"Categories",
		components:{
			Crumbs
		},
		data() {
			return {
				// 
				queryInfo:{
					type:3,
					pagenum:1,
					pagesize:5
				},
				// 
				catelist:[],
				// 
				total:0,
				// table
				columns:[
					{
					label: '',
					prop: 'cat_name',
					},
					{
						label:'',
						// 
						type:'template',
						// 
						template:'isok'
					},
					{
						label:'',
						// 
						type:'template',
						// 
						template:'order'
					},
					{
						label:'',
						// 
						type:'template',
						// 
						template:'opt'
					}
				],
				// 
				addCateDialogVisible:false,
				// 
				addCateForm:{
					// 
					cat_name:'',
					//id
					cat_pid:0,
					//
					cat_level:0
				},
				// 
				addCateFormRules:{
					cat_name:[
						 { required: true, message: '', trigger: 'blur' },
						 { min: 1, max: 10, message: ' 1  10 ', trigger: 'blur' }
					]
				},
				// 
				parentCatrList:[],
				//
				cascaderProps:{
					// 
					expandTrigger: 'hover',
					value:'cat_id',
					label:'cat_name',
					children:'children',
					checkStrictly:true
				},
				// id
				selectdeKeys:[],
				//
				editCateDialogVisible:false,
				// 
				editCateForm:{}
				
				
			}
		},
		created() {
			this.getCateList()
		},
		methods:{
			//
			async getCateList(){
				const {data:res} = await this.$http.get('categories',{params:this.queryInfo})
				if(res.meta.status !== 200){
					return this.$message.error('')
				}
				// console.log(res.data)
				//catelist
				this.catelist = res.data.result
				this.total = res.data.total
			},
			//pagesize
			handleSizeChange(newSize){
				this.queryInfo.pagesize = newSize
				this.getCateList()
			},
			// pagenum	
			handleCurrentChange(newPage){
				this.queryInfo.pagenum = newPage
				this.getCateList()
			},
			// 
			showAddCate(){
				
				// 
				this.getParentCateList()
				this.addCateDialogVisible = true
			},
			// 
			async getParentCateList(){
				const {data:res} = await this.$http.get('categories',{params:{type: 2 }})
				if(res.meta.status !== 200){
					return this.$message.error('')
				}
				// console.log(res.data)
				this.parentCatrList = res.data
			},
			//
			parentCatrChanged(){
				// console.log(this.selectdeKeys)
				//selectdeKeyslength>0 
				if(this.selectdeKeys.length>0){
					// id
					this.addCateForm.cat_pid = this.selectdeKeys[this.selectdeKeys.length-1]
					// 
					this.addCateForm.cat_level = this.selectdeKeys.length
					return
				} else{
					// id
					this.addCateForm.cat_pid = 0
					// 
					this.addCateForm.cat_level = 0
				}
			},
			//
			addCate(){
				// console.log(this.addCateForm)
				this.$refs.addCateFormRef.validate(async valid => {
					if(!valid) return
					const {data:res} = await this.$http.post('categories',this.addCateForm)
					if(res.meta.status !== 201){
						this.$message.error('')
					}
					this.$message.success('')
					this.getCateList()
					this.addCateDialogVisible = false
				})
			},
			//,
			addCateDialogClosed(){
				this.$refs.addCateFormRef.resetFields()
				this.selectdeKeys = []
				this.addCateForm.cat_level = 0
				this.addCateForm.cat_pid = 0
			},
			//
			async showEditCate(id){
				const {data:res} = await this.$http.get('categories/' + id)
				// console.log(res)
				if(res.meta.status !== 200){
					return this.$message.error('')
				}
				this.editCateForm = res.data
				this.editCateDialogVisible = true
			},
			//,
			editCateDialogClosed(){
				this.$refs.editCateFormRef.resetFields()
				this.editCateForm = {}
			},
			//,
			eidtCate(){
				this.$refs.editCateFormRef.validate(async valid => {
					if(!valid) return
					const {data:res} = await this.$http.put('categories/' + this.editCateForm.cat_id,{cat_name:this.editCateForm.cat_name})
					if(res.meta.status !== 200){
						return this.$message.error('')
					}
					this.$message.success('')
					this.getCateList()
					this.editCateDialogVisible = false
				})
			},
			//,
			async removeCate(id){
				const confirmResult = await this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).catch(err => err)
				
				// , confirm
				// , cancel
				// console.log(confirmResult)
				if(confirmResult !== 'confirm'){
					return this.$message.info('')
				}
				
				const {data:res} = await this.$http.delete('categories/' + id)
					if(res.meta.status !== 200){
						return this.$message.error('')
					}
					this.$message.success('')
					this.getCateList()
			},
			
			
			
		}
  }
</script>

<style scoped>
	.bobutton{
		margin-bottom: 0.9375rem;
	}
	.tree-table{
		text-align: center;
	}
	.el-cascader{
		width: 100%;
	}
</style>
