<template>
	<div class="Rights">
		<!--  -->
		<crumbs>
			<b slot="crumbsa"></b>
			<b slot="crumbsb"></b>
		</crumbs>
		<!--  -->
		<el-card>
			<el-table border :data="rightsList">
				<el-table-column align="center" label='' type='index'></el-table-column>
				<el-table-column align="center" label='' prop="authName"></el-table-column>
				<el-table-column align="center" label='' prop="path"></el-table-column>
				<el-table-column align="center" label='' prop="level">
					<template v-slot="scope">
						<el-tag v-if="scope.row.level == '0'"></el-tag>
						<el-tag type="success" v-else-if="scope.row.level == '1'"></el-tag>
						<el-tag type="warning" v-else></el-tag>
					</template>
				</el-table-column>
			</el-table>
		</el-card>
	</div>
</template>

<script>
	import Crumbs from '../common/Crumbs.vue'
  export default{
    name:"Rights",
		components:{
			Crumbs
		},
		data(){
			return {
				//
				rightsList:[]
			}
		},
		methods:{
			async getRightsList(){
			  const {data: res} = await this.$http.get('rights/list')
				// console.log(res)
				if(res.meta.status !== 200){
					return this.$message.error('')
				}
				this.rightsList = res.data
				// console.log(this.rightsList)
			}
		},
		created() {
			//
			this.getRightsList()
		}
  }
</script>

