<template>
	<div class="params">
		<!--  -->
		<crumbs>
			<b slot="crumbsa"></b>
			<b slot="crumbsb"></b>
		</crumbs>
		<!--  -->
		<el-card>
			<!--  -->
			<el-alert :closable="false" title="!" type="warning" show-icon></el-alert>
			<!--  -->
			<el-row class="cta_opt">
				<el-col>
					<span></span>
					<!--  -->
					 <el-cascader
							v-model="selectedCateKeys"
							:options="catelist"
							:props="cateProps"
							@change="handleChange"></el-cascader>
				</el-col>
			</el-row>
			<!-- teb -->
			<el-tabs v-model="activeName" @tab-click="handleTabClick">
				<!--  -->
			    <el-tab-pane label="" name="many">
						<el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true"></el-button>
						<!--  -->
						<el-table :data="manyTableData" border>
							<!--  -->
							<el-table-column align="center" type="expand">
								<template v-slot="scope">
									<!-- tag -->
									<el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i, scope.row)">{{item}}</el-tag>
									<!--  -->
									<el-input
									  class="input-new-tag"
									  v-if="scope.row.inputVisible"
									  v-model="scope.row.inputValue"
									  ref="saveTagInput"
									  size="small"
									  @keyup.enter.native="handleInputConfirm(scope.row)"
									  @blur="handleInputConfirm(scope.row)"
									>
									</el-input>
									<el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
								</template>
							</el-table-column>
							<el-table-column align="center" label="" type="index"></el-table-column>
							<el-table-column align="center" label="" prop="attr_name"></el-table-column>
							<el-table-column align="center" label="">
								<template v-slot="scope">
									<el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)"></el-button>
									<el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)"></el-button>
								</template>
							</el-table-column>
						</el-table>
					</el-tab-pane>
					<!--  -->
			    <el-tab-pane label="" name="only">
						<el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true"></el-button>
						<!--  -->
						<el-table :data="onlyTableData" border>
							<el-table-column align="center" type="expand">
								<el-table-column align="center" type="expand">
									<template v-slot="scope">
										<!-- tag -->
										<el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i, scope.row)">{{item}}</el-tag>
										<!--  -->
										<el-input
										  class="input-new-tag"
										  v-if="scope.row.inputVisible"
										  v-model="scope.row.inputValue"
										  ref="saveTagInput"
										  size="small"
										  @keyup.enter.native="handleInputConfirm(scope.row)"
										  @blur="handleInputConfirm(scope.row)"
										>
										</el-input>
										<el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
									</template>
								</el-table-column>
							</el-table-column>
							<el-table-column align="center" label="" type="index"></el-table-column>
							<el-table-column align="center" label="" prop="attr_name"></el-table-column>
							<el-table-column align="center" label="">
								<template v-slot="scope">
									<el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)"></el-button>
									<el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)"></el-button>
								</template>
							</el-table-column>
						</el-table>
					</el-tab-pane>
			  </el-tabs>
			
			
		</el-card>
		
		<!--  -->
		<el-dialog
		  :title="'' + titleText"
		  :visible.sync="addDialogVisible"
		  width="50%"
			@close="addDialogclosed">
		  <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
		    <el-form-item :label="titleText" prop="attr_name">
		      <el-input v-model="addForm.attr_name"></el-input>
		    </el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="addDialogVisible = false"> </el-button>
		    <el-button type="primary" @click="addParams"> </el-button>
		  </span>
		</el-dialog>
		
		<!--  -->
		<el-dialog
		  :title="'' + titleText"
		  :visible.sync="editDialogVisible"
		  width="50%"
			@close="editdDialogclosed">
		  <el-form :model="editForm" :rules="addFormRules" ref="editFormRef" label-width="100px">
		    <el-form-item :label="titleText" prop="attr_name">
		      <el-input v-model="editForm.attr_name"></el-input>
		    </el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="editDialogVisible = false"> </el-button>
		    <el-button type="primary" @click="editParams"> </el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
	import Crumbs from '../common/Crumbs.vue'
  export default{
    name:"Params",
		components:{
			Crumbs
		},
		data() {
			return {
				//
				catelist:[],
				//
				cateProps:{
					// 
					expandTrigger: 'hover',
					value:'cat_id',
					label:'cat_name',
					children:'children'
				},
				// 
				selectedCateKeys:[],
				//
				activeName:'many',
				//
				manyTableData:[],
				//
				onlyTableData:[],
				//
				addDialogVisible:false,
				// 
				addForm:{
					attr_name:''
				},
				//
				addFormRules:{
					attr_name:[
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editDialogVisible:false,
				//
				editForm:{},
			}
		},
		created() {
			this.getCateList()
		},
		methods:{
			//
			async getCateList(){
				const {data:res} = await this.$http.get('categories')
				if(res.meta.status !== 200){
					return this.$message.error('')
				}
				this.catelist = res.data
				// console.log(this.catelist)
			},
			// 
			handleChange(){
				this.getParamsData()
			},
			// tab
			handleTabClick(){
				// console.log(this.activeName)
				this.getParamsData()
			},
			//
			async getParamsData(){
				// console.log(this.selectedCateKeys)
				//
				if(this.selectedCateKeys.length !== 3){
					this.selectedCateKeys = []
					this.manyTableData = []
					this.onlyTableData = []
					return
					 // this.$message.error('!')
				}
				
				// id,,
				const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{params: {sel:this.activeName}})
				if(res.meta.status !== 200){
					return this.$message.error('')
				}
				
				res.data.forEach(item => {
					item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
					// 
					item.inputVisible = false
					item.inputValue = ''
				})
				
				// console.log(res.data)
				if(this.activeName === 'many'){
					this.manyTableData = res.data
				}else{
					this.onlyTableData = res.data
				}
			},
			//
			addDialogclosed(){
				this.$refs.addFormRef.resetFields()
			},
			//
			addParams(){
				this.$refs.addFormRef.validate(async valid =>{
					if(!valid) return
					const {data:res} = await this.$http.post(`categories/${this.cateId}/attributes`,{attr_name: this.addForm.attr_name, attr_sel:this.activeName})
					if(res.meta.status !== 201){
						return this.$message.error('')
					}	
					this.$message.success('')
					this.addDialogVisible = false
					this.getParamsData()
				})
			},
			// 
			async showEditDialog(attr_id){
				//
				const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes/${attr_id}`,{params:{attr_sel:this.activeName}})
				if(res.meta.status !== 200){
					return this.$message.error('')
				}
				this.editForm = res.data
				this.editDialogVisible = true
			},
			//
			editdDialogclosed(){
				this.$refs.editFormRef.resetFields()
				this.editForm = {}
			},
			//
			editParams(){
				this.$refs.editFormRef.validate(async valid =>{
					if(!valid) return
					const {data:res} = await this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`,{attr_name:this.editForm.attr_name, attr_sel:this.activeName})
					// console.log(res)
					if(res.meta.status !== 200){
						return this.$message.error('')
					}
					this.$message.success('')
					this.getParamsData()
					this.editDialogVisible = false
				})	
			},
			// id
			async removeParams(attr_id){
				 const confirmResult = await this.$confirm(', ?', '', {
				          confirmButtonText: '',
				          cancelButtonText: '',
				          type: 'warning'
				        }).catch(err => err)
								if(confirmResult !== 'confirm'){
									return this.$message.info('')
								}
								
								const {data:res} = await this.$http.delete(`categories/${this.cateId}/attributes/${attr_id}`)
								if(res.meta.status !== 200){
									return this.$message.error('')
								}
								this.$message.success('')
								this.getParamsData()
			},
			// enter
			async handleInputConfirm(row){
				// console.log(111)
				if(row.inputValue.trim().length == 0){
					this.inputValue = ''
					row.inputVisible = false
					return
				}
				//return
				row.attr_vals.push(row.inputValue.trim())
				row.inputValue = ''
				row.inputVisible = false
				//
				this.saveAttrVals(row)
			},
			// 
			showInput(row){
				row.inputVisible = true
				//
				//$nextTick
				this.$nextTick(_ => {
				  this.$refs.saveTagInput.$refs.input.focus();
				})
			},
			//
			handleClose(i, row){
				row.attr_vals.splice(i ,1)
				//
				this.saveAttrVals(row)
			},
			//attr_vals 
			async saveAttrVals(row){
				//
				const {data:res} = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{attr_name:row.attr_name,attr_sel:row.attr_sel,attr_vals:row.attr_vals.join(',')})
				if(res.meta.status !== 200){
					return this.$message.error('')
				}
				this.$message.success('')
			}	
				
			
			
			
		},
		computed:{
			// true
			isBtnDisabled(){
				if(this.selectedCateKeys.length !== 3){
					return true
				}
				return false
			},
			//Id
			cateId(){
				if(this.selectedCateKeys.length == 3){
					return this.selectedCateKeys[2]
				}
				return null
			},
			//
			titleText(){
				if(this.activeName === 'many'){
					return ''
				}
				return ''
			}
			
			
			
			
			
		},
  }
</script>

<style lang="less" scoped>
	.cta_opt{
		margin: 0.9375rem 0;
	}
	.el-tag{
		margin: 0.625rem;
	}
	.input-new-tag{
		width: 9.375rem;
	}
</style>
