<template>
	<div class="login_container">
		<div class="login_box">
			<!--  -->
			<div class="avatar_box">
				<img src="../assets/logo.png" alt="">
			</div>
			<!--  -->
				<el-form ref="loginFormRef" :rules="loginFormRules" :model="loginForm" label-width="0px" class="login_form">
					<!--  -->
				  <el-form-item prop="username">
				    <el-input v-model="loginForm.username" prefix-icon="el-icon-user"></el-input>
				  </el-form-item>
					<!--  -->
					<el-form-item prop="password">
					  <el-input show-password v-model="loginForm.password" prefix-icon="el-icon-lock"></el-input>
					</el-form-item>
					<!--  -->
					<el-form-item class="btns">
					  <el-button type="primary" @click="login"></el-button>
						<el-button type="info" @click="resetLoginForm"></el-button>
					</el-form-item>
				</el-form>
		</div>
	</div>
</template>

<script>
  export default{
    name:"Login",
		data(){
			return{
				// 
				loginForm:{
					username:'admin',
					password:'123456'
				},
				// 
				loginFormRules:{
					username:[
						{ required: true, message: '', trigger: 'blur' },
						{ min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
					],
					password:[
						{ required: true, message: '', trigger: 'blur' },
						{ min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
					]
				}
			}
		},
		methods:{
			// ,
			resetLoginForm(){
				this.$refs.loginFormRef.resetFields()
			},
			login(){
				this.$refs.loginFormRef.validate(async valid => {
					if(!valid) return
					const {data: res} = await this.$http.post('login',this.loginForm)
					if(res.meta.status !== 200){
						return this.$message.error('')
					}
					this.$message.success('')

					window.sessionStorage.setItem('token',res.data.token)
					this.$router.push('/home')
				})
			}
		}
  }
</script>

